          <p>Якщо <strong>Ви - Університет</strong>, то Ви можете переглядати відгуки про Ваш та інші ВНЗ. Тут Ви знайдете конструктивні зауваження та компліменти.
Якщо Ви бажаєте, щоб на сторінці <strong><em style={{"color": "rgb(83, 98, 255)"}}>UNISTATS</em></strong> зі статистикою Вашого університету були посилання на Ваші сторінки в соцмережах чи офіційний сайт, пропонуємо Вам купити підписку. Для її купівлі - заповніть форму нижче.</p>
