import {useState} from 'react' 

const SearchbarLecturers = ({data}) => {
    const UniList = data.map(obj =>{ return { name: obj['name'], id :obj['teacher_id'] } });
    console.log(UniList)
    let IdList = []
    let NameList = []
    for(let obj of UniList){
        if(!NameList.includes(obj['name'])){
            NameList.push(obj['name'])
            IdList.push(obj['id'])
        }
    }
    const [propositions, setPropositions] = useState([])
    const [searchbar, setSearchbar] = useState('')
    function onSearchBarChange(target){
        setSearchbar(target.value)
        let Propositions = []
        let temp = target.value.split(" ")
        for(let part of temp){
            part = part.toLowerCase()
            for(let index in NameList){
                if (NameList[index].toLowerCase().includes(part) && !Propositions.includes([NameList[index], IdList[index]]) && part!==''){
                    Propositions.push([NameList[index], IdList[index]])
                }
            }
        }
        setPropositions(Propositions)
    }
    function handleEnter(e){
        if(e.key === 13){
            e.preventDefault()
            return false
        } 
    }
    return (
        <section class="forms">
              <div class="container-fluid" style={{'width':'70%', 'margin':'0px auto'}}>
                <div class="row" >
                    <div class="col-md-12">
                        <div class="section-heading" style={{'float':'left'}}>
                            <h2>Search Lecturer</h2>
                        </div>
                    </div>
                    <form method="post" id='contact'>
                        <div className='col-md-6' style={{'width':'100%', 'margin':'0px auto'}}>
                            <fieldset className='center'>
                                <input id="name" type="text" placeholder='Type in some letters...' onChange={(e) => onSearchBarChange(e.target)} onKeyPress={e => handleEnter(e)} value={searchbar} class='form-control'/>
                            </fieldset>
                        </div>
                        {
                            propositions.map(value => <div class="border-rectangle-button"><a href={"/lecturer_"+value[1]}>{value[0]}</a></div>)
                        }
                    </form>
                </div>
            </div>
        </section>
    )
}

export default SearchbarLecturers
