import {useState} from 'react' 

const SearchbarUniversities = ({data}) => {
    let UniList = data.map(obj =>{ return { name: obj['name'].replaceAll('&#39;', "'") , id :obj['university_id'] } });
    console.log('UniList: ' + UniList)
    console.log(UniList)
    const [propositions, setPropositions] = useState([])
    const [searchbar, setSearchbar] = useState('')
    function onSearchBarChange(target){
        setSearchbar(target.value)
        let Propositions = []
        let temp = target.value.split(" ")
        for(let part of temp){
            part = part.toLowerCase()
            for(let obj of UniList){
                if (obj['name'].toLowerCase().includes(part) && !Propositions.includes([obj['name'], obj['id']]) && part!==''){
                    console.log('objt: ' + [obj['name'], obj['id']])
                    console.log('Propos: ' + Propositions)
                    Propositions.push([obj['name'], obj['id']])
                }
            }
        }
        console.log(Propositions)
        setPropositions(Propositions)
    }
        
    function handleEnter(e){
        if(e.key === 13){
            e.preventDefault()
            return false
        } 
    }
    return (
        <section class="forms">
              <div class="container-fluid" style={{'width':'70%', 'margin':'0px auto'}}>
                <div class="row" >
                    <div class="col-md-12">
                        <div class="section-heading" style={{'float':'left'}}>
                            <h2>Search University</h2>
                        </div>
                    </div>
                    <form method="post" id='contact'>
                        <div className='col-md-6' style={{'width':'100%', 'margin':'0px auto'}}>
                            <fieldset className='center'>
                                <input id="name" type="text" placeholder='Type in some letters...' onChange={(e) => onSearchBarChange(e.target)} onKeyPress={e => handleEnter(e)} value={searchbar} class='form-control'/>
                            </fieldset>
                        </div>
                        {
                            propositions.map(value => <div class="border-rectangle-button"><a href={"/university_"+value[1]}>{value[0]}</a></div>)
                        }
                    </form>
                </div>
            </div>
        </section>
    )
}

export default SearchbarUniversities
