import React from 'react'
import {useParams} from 'react-router-dom'

const University = ({data}) => {
    let params = useParams()
    let imitated_data = {"is_logged": true, 
    "university": {"domain": "ucu.edu.ua", 
    "logotype": null, 
    "name": "Український Католицький Університет", 
    "university_id": "unv-16185-0rsESB-99209"}}
    imitated_data = imitated_data["university"]
    if(imitated_data["university_id"]==="unv-16185-0rsESB-99209"){
        imitated_data.logotype = 'https://s3.eu-central-1.amazonaws.com/ucu.edu.ua/wp-content/uploads/sites/9/2017/03/uku_logo__seal-300x300.png'
    }else{
        imitated_data.logotype = 'https://cdn1.pokupon.ua/uploaded/merchant_pages/7966/logos/original/logo-NaUKMA.png?1491224305'
    }
    return (
      <div className='lecturer'>
        <section class="left-image">
                <div class="container-fluid">
                <div class="row">
                    <div class="col-md-6">
                    <img src={imitated_data.logotype} alt="" />
                    </div>
                    <div class="col-md-6">
                    <div class="right-content" style={{'margin-top':'40px'}}>
                        <h1>{imitated_data.name}</h1>
                    </div>
                    </div>
                </div>
                </div>
        </section>
        <section class="top-image">
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-8" style={{"margin":'50px auto'}}>
              <img src="assets/images/top-image.jpg" alt="" />
              <div class="down-content">
                <h4>Stats</h4>
                <p>Waiting for stats.....</p>
              </div>
            </div>
          </div>
        </div>
      </section>
      </div>
    )
}

export default University

